 body {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   color: var(--color1);
   overflow-x: hidden;
   background: var(--bg-color1);
   font-family: 'Open Sans', sans-serif;
   font-size: 14px;
   user-select: none;
 }

 :root {
   --bg-color1: #121212;
   --bg-color2: #141414;
   --bg-color3: #363636;
   --bg-color4: #1D1D1D;
   --bg-color5: #434343;
   --color1: #FFFFFF;
   --color2: #838383;
 }

 body::-webkit-scrollbar-thumb {
   background: rgb(197, 197, 197);
   border-radius: 5px;
 }
 

 body::-webkit-scrollbar {
    width: 8px;
   background-color: var(--bg-color5);
 }

 *::-webkit-scrollbar {
   width: 10px;
 }

 *::-webkit-scrollbar-thumb {
   background: grey;
   border-radius: 5px;
 }

 .App {
   display: grid;
   grid-template-columns: 1fr 5fr;
 }

 @media only screen and (max-width: 968px) {

   .App,
   .homepage {
     display: unset;
   }
 }